import React, { useState } from 'react';
import { Typography, Card, Form, Input, Button, message } from 'antd';
import { OrderForm as OrderFormType } from '../types';
import './OrderFormStep.css';

const { Title, Text } = Typography;
const { TextArea } = Input;

interface OrderFormStepProps {
  orderData: OrderFormType;
  onBack: () => void;
  onSubmit: (formData: any) => void;
}

const OrderFormStep: React.FC<OrderFormStepProps> = ({
  orderData,
  onBack,
  onSubmit
}) => {
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (values: any) => {
    setLoading(true);
    try {
      const finalOrderData = { ...orderData, ...values };
      
      //    Telegram 
      if (window.Telegram?.WebApp) {
        const data = JSON.stringify(finalOrderData);
        window.Telegram.WebApp.sendData(data);
        
        //    
        window.Telegram.WebApp.showAlert('  !       .');
        
        //  -
        setTimeout(() => {
          window.Telegram.WebApp.close();
        }, 2000);
      } else {
        // Fallback    Telegram
        console.log('Order data:', finalOrderData);
        message.success('  !');
        onSubmit(finalOrderData);
      }
    } catch (error) {
      console.error('Error submitting order:', error);
      message.error('   .   .');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="order-form-step">
      <div className="step-header">
        <Title level={3}>  </Title>
        <Text type="secondary">      </Text>
      </div>

      <div className="form-content">
        <Card className="form-card">
          <Form
            form={form}
            layout="vertical"
            onFinish={handleSubmit}
            initialValues={{
              customerName: orderData.customerName,
              customerPhone: orderData.customerPhone,
              customerAddress: orderData.customerAddress
            }}
          >
            <Form.Item
              label=""
              name="customerName"
              rules={[
                { required: true, message: ',   ' },
                { min: 2, message: '    2 ' }
              ]}
            >
              <Input 
                placeholder="  "
                size="large"
                className="form-input"
              />
            </Form.Item>

            <Form.Item
              label=""
              name="customerPhone"
              rules={[
                { required: true, message: ',   ' },
                { 
                  pattern: /^(\+7|7|8)?[\s-]?\(?[489][0-9]{2}\)?[\s-]?[0-9]{3}[\s-]?[0-9]{2}[\s-]?[0-9]{2}$/,
                  message: '   '
                }
              ]}
            >
              <Input 
                placeholder="+7 (999) 123-45-67"
                size="large"
                className="form-input"
              />
            </Form.Item>

            <Form.Item
              label=" "
              name="customerAddress"
              rules={[
                { required: true, message: ',   ' },
                { min: 10, message: '    10 ' }
              ]}
            >
              <TextArea 
                placeholder="   "
                rows={3}
                className="form-textarea"
              />
            </Form.Item>

            <Form.Item
              label=" "
              name="additionalNotes"
              initialValue={orderData.additionalNotes}
            >
              <TextArea 
                placeholder="   ,     .."
                rows={4}
                className="form-textarea"
              />
            </Form.Item>

            <div className="form-actions">
              <Button 
                type="default" 
                size="large" 
                onClick={onBack}
                className="back-button"
              >
                
              </Button>
              <Button 
                type="primary" 
                size="large" 
                htmlType="submit"
                loading={loading}
                className="submit-button"
              >
                 
              </Button>
            </div>
          </Form>
        </Card>
      </div>
    </div>
  );
};

export default OrderFormStep; 